export const TYPES_ENUM = {
  FILM: '',
  TV_SERIES: '',
  VIDEO: '',
  TV_SHOW: '-',
  MINI_SERIES: '-',
  ANIME: ''
}

export const USER_LIST_TYPES_ENUM = {
  FAVORITE: 'favorite',
  LATER: 'later',
  WATCHING: 'watching',
  COMPLETED: 'completed',
  ABANDONED: 'abandoned',
  HISTORY: 'history'
}

export const handleApiError = (error) => {
  if (error.code === 'ECONNABORTED') {
    return {
      message: ':    ()',
      code: 408
    }
  } else if (error.response) {
    if (error.response.status >= 500) {
      return {
        message: '  . ,  ',
        code: error.response.status
      }
    }

    switch (error.response.status) {
      case 403:
        return {
          message: ',    ',
          code: 403
        }
      case 404:
        return {
          message: '  ',
          code: 404
        }
      case 401:
        return {
          message: ' ,  ',
          code: 401
        }
      default:
        return {
          message: `  . : ${error.response.data?.status ?? error.response.status}`,
          code: error.response.status
        }
    }
  } else {
    return {
      message: `: ${error.message}`,
      code: null
    }
  }
}
